.footer {
  background: #121212;
  @include tablet {
    padding-top: 50px;
  }
  &__container {
    display: flex;
    align-items: center;
    padding-right: max(15px, calc((100% - 1300px) / 2));
    @include tablet {
      flex-direction: column;
      flex-direction: column-reverse;
      gap: 50px;
      padding: 0 15px;
    }
  }
  &__content {
    max-width: 646px;
  }
  &__title {
    margin: 0;
    max-width: 550px;
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 700;
    font-size: 32px;
    line-height: 55px;
    color: var(--white);
    margin-bottom: 35px;
  }
  &__text {
    margin: 0;
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 170%;
    color: var(--white);
  }
}
.form {
  margin-top: 50px;
  width: 100%;
  display: flex;
  flex-direction: column;
  &__input {
    background: transparent;
    width: 100%;
    border: none;
    margin-bottom: 20px;
    outline: none;
    border-bottom: 1px solid var(--white);
    padding-bottom: 14px;
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 170%;
    color: var(--white);
    &::placeholder {
      font-family: "Montserrat";
      font-style: normal;
      font-weight: 400;
      font-size: 16px;
      line-height: 170%;
      color: var(--white);
    }
  }
  &__text {
    background: transparent;
    resize: none;
    outline: none;
    width: 100%;
    border: none;
    border-bottom: 1px solid var(--white);
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 170%;
    color: var(--white);
    &::placeholder {
      font-family: "Montserrat";
      font-style: normal;
      font-weight: 400;
      font-size: 16px;
      line-height: 170%;
      color: var(--white);
    }
  }
  &__btn {
    margin-top: 68px;
    display: flex;
    justify-content: flex-end;
    &-item {
      position: relative;
      z-index: 5;
      cursor: pointer;
      background: transparent;
      border: none;
      font-family: "Montserrat";
      font-style: normal;
      font-weight: 400;
      font-size: 16px;
      line-height: 150%;
      color: var(--white);
      &::before {
        content: " ";
        position: absolute;
        z-index: -1;
        width: 70px;
        height: 70px;
        background: #1378ef;
        left: -25px;
        top: -19px;
        border-radius: 100%;
      }
    }
  }
}
