.projects {
  padding-top: 150px;
  padding-bottom: 50px;
  &__title {
    margin-bottom: 20px;
  }
  &__sub-title {
    margin-bottom: 55px;
  }
  &__write {
    max-width: 668px;
    margin: 0 auto;
    position: relative;
    margin-top: 150px;
    &-header {
      position: relative;
      margin: 0;
      font-family: "Montserrat";
      font-style: normal;
      font-weight: 700;
      font-size: 46px;
      line-height: 55px;
      text-align: center;
      color: var(--white);
      margin-bottom: 9px;
      z-index: 5;
    }
    &-text {
      position: relative;
      margin: 0;
      font-family: "Montserrat";
      font-style: normal;
      font-weight: 400;
      font-size: 16px;
      line-height: 150%;
      text-align: center;
      color: var(--white);
      z-index: 5;
    }
    &-col {
      width: 260px;
      height: 260px;
      border-radius: 100%;
      background: #1378ef;
      position: absolute;
      top: 100%;
      left: 50%;
      transform: translate(-50%, -50%);
      z-index: 0;
    }
  }
}
.tabs {
  &__nav {
    display: flex;
    flex-wrap: wrap;
    column-gap: 50px;
    row-gap: 30px;
    &-item {
      cursor: pointer;
      font-family: "Montserrat";
      font-style: normal;
      font-weight: 400;
      font-size: 22px;
      line-height: 150%;
      color: rgba(255, 255, 255, 0.59);
    }
    &-active {
      color: var(--white);
    }
  }
  &__title {
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 700;
    font-size: 28px;
    line-height: 55px;
    color: var(--white);
    max-width: 435px;
    width: 100%;
    @include small-tablet {
      font-size: 22px;
    }
  }

  &__panel {
    color: white;
    justify-content: center;
    font-size: 70px;
    @include small-tablet {
      flex-direction: column;
    }
  }
  &__content {
    margin-top: 35px;
    &-title {
      font-family: "Montserrat";
      font-style: normal;
      font-weight: 700;
      font-size: 28px;
      line-height: 55px;
      color: var(--white);
    }
    &-text {
      font-family: "Montserrat";
      font-style: normal;
      font-weight: 400;
      font-size: 16px;
      line-height: 170%;
      color: var(--white);
    }
  }
  &__accordion {
    display: flex;
    flex-direction: column;
    width: 100%;
    gap: 50px;
    @include small-tablet {
      margin-top: 50px;
    }
    &-trigger {
      display: flex;
      justify-content: space-between;
      &.content-active {
        .tabs__accordion-title {
          color: blue;
        }
        .tabs__accordion-text {
          display: block;
          transition: 0.5s;
          opacity: 1;
        }
        .tabs__accordion-arrow {
          img {
            transition: 0.5s;
            transform: rotate(90deg);
          }
        }
      }
    }
    &-content {
      display: flex;
      flex-direction: column;
    }
    &-title {
      cursor: pointer;
      font-family: "Montserrat";
      font-style: normal;
      font-weight: 700;
      font-size: 28px;
      line-height: 55px;
      color: var(--white);
      width: 100%;
      @include small-tablet {
        font-size: 17px;
      }
    }
    &-text {
      transition: 0.5s;
      opacity: 0;
      font-family: "Montserrat";
      font-style: normal;
      font-weight: 400;
      font-size: 16px;
      line-height: 170%;
      color: var(--white);
      @include small-tablet {
        font-size: 16px;
      }
    }
    &-arrow {
      padding: 15px 0;
      img {
        transition: 0.5s;
        display: block;
      }
    }
  }
}
