.hero {
  padding: 300px 0 250px 0;
  @include tablet {
    padding: 200px 0;
  }
  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include tablet {
      flex-direction: column;
      gap: 70px;
    }
  }
  &__content {
    max-width: 416px;
    @include tablet {
      max-width: 700px;
    }
  }
  &__title {
    margin: 0;
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 700;
    line-height: 116%;
    font-size: 78px;
    @include tablet {
      font-size: 60px;
    }
    color: var(--white);
    margin-bottom: 24px;
  }
  &__descr {
    margin: 0;
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 400;
    font-size: 30px;
    line-height: 137%;
    color: var(--white);
    margin-bottom: 50px;
    @include tablet {
      font-size: 25px;
    }
  }
  &__scils {
    display: flex;
    flex-wrap: wrap;
    row-gap: 24px;
    column-gap: 20px;
    &-item {
      margin: 0;
      padding: 2px 16px;
      background: #1378ef;
      border-radius: 64px;
      font-family: "Montserrat";
      font-style: normal;
      font-weight: 400;
      font-size: 21px;
      line-height: 150%;
      color: var(--white);
      @include tablet {
        font-size: 14;
      }
    }
  }
  &__convas {
    @include mobile {
      &-item {
        width: 250px;
        height: 250px;
      }
    }
  }
}
