.reviews {
  padding: 150px 0;
  &__title {
    margin-bottom: 20px;
  }
  &__sub-title {
    margin-bottom: 90px;
  }
  &__content {
    display: flex;
    @include tablet {
      flex-direction: column;
      justify-content: center;
    }
  }
  &__swiper {
    max-width: 860px;
    margin: 0;
    @include tablet {
      margin-bottom: 70px;
    }
    &-item {
      background: rgba(24, 24, 24, 0.59);
      padding: 35px 25px;
      height: 100%;
    }
    &-img {
      margin-bottom: 10px;
      display: flex;
      justify-content: center;
    }
    &-name {
      font-family: "Montserrat";
      font-style: normal;
      font-weight: 700;
      font-size: 20px;
      text-align: center;
      line-height: 170%;
      color: var(--white);
      margin-bottom: auto;
    }
    &-descr {
      margin-top: 10px;
      font-family: "Montserrat";
      font-style: normal;
      font-weight: 400;
      font-size: 16px;
      line-height: 170%;
      color: var(--white);
    }
  }
  &__arrow {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-end;
    gap: 10px;
    margin-left: 100px;
    @include tablet {
      justify-content: center;
      align-items: center;
      flex-direction: row;
      gap: 50px;
      margin-left: 0;
    }
    &-prev {
      display: flex;
      justify-content: center;
      align-items: center;
      background: #1378ef;
      width: 70px;
      height: 70px;
      border-radius: 100%;
      transform: matrix(-1, 0, 0, 1, 0, 0);
      img {
        position: relative;
        left: -30px;
        transform: rotate(180deg);
        transition: 0.5s;
      }
      &:hover {
        transition: 0.5s;
        img {
          left: 0;
        }
      }
    }
    &-next {
      display: flex;
      justify-content: center;
      align-items: center;
      background: #1378ef;
      width: 70px;
      height: 70px;
      border-radius: 100%;
      transform: matrix(-1, 0, 0, 1, 0, 0);

      img {
        position: relative;
        right: -30px;
        transform: rotate(180deg);
        transition: 0.5s;
      }
      &:hover {
        transition: 0.5s;
        img {
          right: 0;
        }
      }
    }
  }
}
