.about {
  &__title {
    margin-bottom: 15px;
  }
  &__sub-title {
    margin-bottom: 45px;
    @include mobile {
      font-size: 35px;
    }
  }
}
.history {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  gap: 35px;
  &__item {
    flex: 0 0 30%;
    @include tablet {
      flex: 0 0 45%;
    }
    @include mobile {
      flex: 1 1 auto;
    }
  }
  &__data {
    max-width: 75px;
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 700;
    font-size: 32px;
    line-height: 170%;
    color: var(--white);
    position: relative;
    margin-bottom: 21px;
    &::after {
      content: "";
      display: block;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 1px;
      background: var(--white);
    }
  }
  &__descr {
    margin: 0;
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 170%;
    color: var(--white);
  }
}
