.header {
  padding: 30px 0;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  &__logo {
    z-index: 50;
    img {
      width: 100%;
    }
  }
}
.nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  &__list {
    display: flex;
    gap: 50px;
  }
  &__item {
    position: relative;
    &:hover::before {
      width: 100%;
      transition: 0.5s;
    }
    &::before {
      content: "";
      position: absolute;
      bottom: -4px;
      width: 0%;
      height: 2px;
      background: white;
      transition: 0.5s;
    }
  }
  &__link {
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    color: var(--white);
    position: relative;
  }
}
.burger {
  display: none;
  @include burger;
}
@include tablet {
  .burger {
    z-index: 50;
    display: block;
  }
  .nav__list {
    position: absolute;
    top: 0;
    left: 0;
    background-color: var(--dark-gray);
    width: 100%;
    height: 100vh;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    z-index: 10;
    transform: translateX(-100%);
    backface-visibility: hidden;
    transition: 0.5s;
  }
  .menu--active {
    transform: translateX(0);
    backface-visibility: visible;
  }
}
